import React from 'react';
import ReactDOM from 'react-dom/client'; 
import { initializeApp } from 'firebase/app';
import ToolApp from './ToolApp'; 

// --- IMPORTANT: Replace with your actual Firebase config ---
const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID
};

// Optionally, throw if any are missing (for dev safety)
Object.entries(firebaseConfig).forEach(([key, value]) => {
  if (!value) {
    throw new Error(`Missing Firebase config value for ${key}. Did you set your .env file?`);
  }
});

// Initialize Firebase
initializeApp(firebaseConfig);

// Placeholder org and permissions for standalone mode
const placeholderOrg = { id: 'dev-org', name: 'Dev Org (Standalone)' };
const placeholderPermissions = ['read', 'write', 'admin']; 

const rootElement = document.getElementById('root');

if (rootElement) {
  const root = ReactDOM.createRoot(rootElement);
  root.render(
    <React.StrictMode>
      {/* Render ToolApp with standalone Firebase instances and placeholder user/org/permissions */}
      <ToolApp
        user={null} 
        org={placeholderOrg}
        permissions={placeholderPermissions}
      />
    </React.StrictMode>,
  );
} else {
  console.error('Failed to find the root element');
}

export {}; 
